	module.exports = {"data":{"markdownRemark":{"html":"<h1>Any Type</h1>\n<p>The Any type instructs Typescript to suspend type checking for the specified variables. Useful when working with dynamic content for which you don’t know the type, and for transitioning your codebase for Javascript to Typescript in pieces. You can use Javascript’s implicit typing with variables declared with a type of Any.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-typescript\"><code>  <span class=\"token keyword\">let</span> notSure<span class=\"token punctuation\">:</span> <span class=\"token keyword\">any</span> <span class=\"token operator\">=</span> <span class=\"token number\">4</span><span class=\"token punctuation\">;</span>\n  notSure <span class=\"token operator\">=</span> <span class=\"token string\">\"maybe a string instead\"</span><span class=\"token punctuation\">;</span>\n  notSure <span class=\"token operator\">=</span> <span class=\"token keyword\">false</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>","fields":{"slug":"/typescript/any-type/"},"frontmatter":{"title":"Any Type"}}},"pathContext":{"slug":"/typescript/any-type/"}}

